import mock from './mock.js'

// vue.config.js
const useProxy = false

// mock
const useMockIntercept = false

// 852 90984154   123456
// 852 93144931   123456
// emilylobsenz@cdnis.edu.hk   123456
// beatapatlewicz@cdnis.edu.hk   123456
// donaldmacpherson@cdnis.edu.hk   123456

let base_url = 'https://srdz.feixianggame.com/icapp/'

let eventCb = null

let bluetooth = 'false'

let mac = null

function delay (cb, result, time = 2000) {
  setTimeout(function () {
    cb(result)
  }, time)
}

function emit (type, data) {
  if (window[eventCb]) {
    window[eventCb](JSON.stringify({
      type,
      data
    }))
  }
}

function callHandler (funcName, data, cb) {
  switch (funcName) {
    case 'getNativeOS':
      cb('ios')
      break
    case 'isBluetoothOpen':
      cb(bluetooth)
      break
    case 'openSysSetting':
      cb(bluetooth = 'true')
      emit(102)
      break
    case 'scanQRCode':
      delay(cb, JSON.stringify({
        type: 1,
        message: `G1MW99301825C#${ mock.random.string('upper', 12) }#42C89500`
      }))
      break
    case 'ringUp':
      log(`will call ${ data.phoneNumber }`)
      break
    case 'isBraceletConnected':
      delay(cb, JSON.stringify(mac ? {
        type: true,
        mac
      } : {
        type: false
      }))
      break
    case 'disConnectBracelet':
      mac = null
      delay(cb, 'true')
      break
    case 'connectBracelet':
      mac = data.mac
      delay(cb, 'true')
      break
    default:
      break
  }
}

setTimeout(function () {
  if (!window.JsNativeApi.isReady && window.WVJBCallbacks) {
    window.WVJBCallbacks[0]({
      registerHandler: function (callback) {
        eventCb = callback
      },
      callHandler
    })
  }
}, 5000)

if (useProxy) {
  base_url = '/'
}

if (useMockIntercept) {
  mock.addIntercept(base_url)
}

export default {
  log,
  base_url,
  timeout: 15000
}

let logElement = null
let toggle = false

export function log (str) {
  if (typeof window.console !== 'undefined' && console.log) {
    console.log(str)
  }
  if (!logElement) {
    let wrapperElement = document.createElement('div')
    wrapperElement.style = 'position: absolute; top: 0; bottom: 0; left: -60%; width: 60%; font-size: 12px; color: #f00; break-all; background-color: rgba(0, 0, 0, 0.3);'
    logElement = document.createElement('div')
    logElement.style = 'position: absolute; top: 0; bottom: 0; left: 0; right: 0; overflow: auto; font-size: 12px; word-break: break-all; background-color: rgba(0, 0, 0, 0.3);'
    wrapperElement.appendChild(logElement)
    let toggleElement = document.createElement('div')
    toggleElement.style = 'position: absolute; bottom: 0; right: -20px; width: 20px; height: 20px; background-color: #f00; border-top-right-radius: 100%;'
    toggleElement.onclick = function () {
      toggle = !toggle
      wrapperElement.style.left = toggle ? '0px' : '-60%'
    }
    wrapperElement.appendChild(toggleElement)
    document.body.appendChild(wrapperElement)
  }
  let element = document.createElement('div')
  element.innerHTML = `${ str }</br>----------`
  logElement.insertBefore(element, logElement.childNodes[0])
}
