<template>
  <navigation-wrapper :title="$t('home.menu.kids')">
    <div class="l-navigation__content has-padding">
      <ul>
        <li class='c-account-info'>
          <div class="c-account-info__name u-more">{{ name }}</div>
          <div v-if="figure" class="c-account-info__avatar" :class="{ 'c-avatar--boy': sex === 1, 'c-avatar--girl': sex === 2,  'has-avatar': !!figure.headUrl }" @click="upload">
            <div class="c-avatar__icon">
              <i :style="{ backgroundImage: `url(${ figure.headUrl })` }"></i>
            </div>
          </div>
        </li>
        <li class="c-info-item">
          <div class="c-info-item__label">{{ $t('account.relationship') }}</div>
          <div class="c-info-item__value">{{ relationship }}</div>
        </li>
        <li class="c-info-item">
          <div class="c-info-item__label">{{ $t('account.student') }}</div>
          <div class="c-info-item__value">{{ studentId }}</div>
        </li>
        <li class="c-info-item" @click="changeBus">
          <div class="c-info-item__label">{{ $t('account.bus') }}</div>
          <div class="c-info-item__value u-more">{{ bus }}</div>
        </li>
      </ul>
      <input class="u-display-none" ref="fileUpload" type="file" accept="image/*" @change="handleFile" />
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import navigationWrapper from '@/views/wrapper/NavigationWrapper.vue'

export default {
  name: 'children-info',
  components: {
    navigationWrapper
  },
  data () {
    return {
      cancelSource: null
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['getFigure']),
    id () {
      return Number(this.$route.params.id)
    },
    figure () {
      return this.getFigure(this.id)
    },
    name () {
      return this.figure ? this.figure.firstName : null
    },
    sex () {
      return this.figure ? this.figure.relationship : 1
    },
    relationship () {
      return this.figure ? this.figure.relationship === 1 ? this.$t('relationship.son') : this.$t('relationship.daughter') : null
    },
    studentId () {
      return this.figure ? this.figure.studentId : null
    },
    bus () {
      return this.figure ? this.figure.busId : null
    }
  },
  methods: {
    ...mapActions(['updateFigureInfo']),
    upload () {
      if (this.figure && this.$refs.fileUpload && !this.cancelSource) {
        this.$refs.fileUpload.click()
      }
    },

    clear () {
      this.cancelSource = null
      this.$refs.fileUpload.value = null
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      this.cancelSource = this.$http.cancelSource()
      this.$http.uploadFile('common/pic/upload', file, {
        lock: true,
        cancelToken: this.cancelSource.token
      }).then(res => {
        this.clear()
        this.changeAvatar(res.data.realPath)
      }, () => {
        this.clear()
      })
    },
    changeAvatar (path) {
      this.cancelSource = this.$http.cancelSource()
      this.$http.post('parent/child/modifyPic', {
        token: this.user.token,
        idStudent: this.id,
        picImgPath: path
      }, {
        lock: true,
        cancelToken: this.cancelSource.token
      }).then(() => {
        this.cancelSource = null
        this.updateFigureInfo({
          id: this.id,
          info: {
            headUrl: path
          }
        })
      }, () => {
        this.cancelSource = null
      })
    },
    changeBus () {
      if (this.figure) {
        this.$router.push({ name: 'modify-bus', params: { id: this.figure.id } })
      }
    }
  },
  beforeDestroy () {
    if (this.cancelSource) {
      this.cancelSource.cancel('destroy')
    }
  }
}
</script>
